@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
.base-button {
  padding: 5px;
  border: 1px solid;
  border-radius: 3px;
  background-color: transparent;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: all .2s ease-in;
}

.base-button:hover {

}

.base-input {
  padding: 5px;
  border: 1px solid;
  border-radius: 3px;
  background-color: transparent;
  font-size: 16px;
}

.spinner {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: conic-gradient(var(--blue), transparent);

  -webkit-animation: rotating 1s linear infinite;

          animation: rotating 1s linear infinite;
}

.spinner::before {
  content: '';
  position: absolute;
  width: 30px;
  height: 30px;
  top: 10px;
  left: 10px;
  border-radius: 50%;
  /*background: white;*/
}

@-webkit-keyframes rotating {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes rotating {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);

    }
    to {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

:root {
  --blue: #437282;
}

*, html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  color: #555;
}

#root {
  /*background-color: #bcdece;*/
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  min-width: 320px;
  max-width: 700px;
  margin: 0 auto;
  border: 1px solid #437282;
  border: 1px solid var(--blue);
  background-color: #fff;
}

.container {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

/* Navbar */
.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 50px;
  padding: 10px 20px;
  background-color: #437282;
  background-color: var(--blue);
  color: #fff;
}

.navbar__header {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #fff;
}

.navbar__btns {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}
.navbar__btn, .navbar__login {
  border-color: #fff;
  color: #fff;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.navbar__btn:hover {
  background-color: #d3e6f3;
  color: #437282;
  color: var(--blue);
}

/* Login */
.login-form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 50%;
  padding: 20px 15px;
  border: 1px solid #437282;
  border: 1px solid var(--blue);
  border-radius: 3px;
}

.login-form__header {
  text-align: center;
}

.login-form__input, .login-form__btn {
  margin-top: 15px;
  padding: 10px;
}
.login-form__btn {
  background-color: #437282;
  background-color: var(--blue);
  border-color: #437282;
  border-color: var(--blue);
  color: #fff;
}
.login-form__btn:hover {
  opacity: .9;
}

/* Chat */
.chat {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  overflow-y: hidden;
}

.chat__dashboard {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding: 10px 20px 50px;
  background-color: #bcdece;
  overflow-y: auto;
}

.chat__new-message {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  padding: 5px 20px;
  background-color: #fff;
}

.chat__input {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  outline: none;
  border: none;
}

.chat__add-btn {
  margin-left: 10px;
  padding: 10px 5px;
  background-color: #437282;
  background-color: var(--blue);
  border-color: #437282;
  border-color: var(--blue);
  color: #fff;
}
.chat__add-btn:hover {
  opacity: .9;
}

/* Message */
.message {
  display: -webkit-flex;
  display: flex;
  padding: 10px 0px;
}
.message__icon {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.message__icon img {
  width: 30px;
}
.message__text-right, .message__text-left {
  position: relative;
  margin: 0 5px;
  padding: 10px;
  max-width: calc(100% - 70px);
  background-color: #fff;
  border-radius: 5px;
}
.message__text-right {
  background-color: #e4fddd;
}
.message__text-left::before {
  content: '';
  position: absolute; /* Абсолютное позиционирование */
  left: -10px; bottom: 5px; /* Положение треугольника */
  border: 5px solid transparent; /* Прозрачные границы */
  border-right: 5px solid #fff /* Добавляем треугольник */
}
.message__text-right::before {
  content: '';
  position: absolute; /* Абсолютное позиционирование */
  right: -10px; bottom: 5px; /* Положение треугольника */
  border: 5px solid transparent; /* Прозрачные границы */
  border-left: 5px solid #e4fddd /* Добавляем треугольник */
}
.message__text-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  grid-gap: 5px;
  gap: 5px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 0 5px 0;
}
.message__author, .message__date {
  font-weight: bold;
  font-size: 14px;
  color: #437282;
  color: var(--blue);
}
.message__date {
  font-size: 12px;
  color: #999;
}

.reverse {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}

@media (max-width: 768px) {
  .navbar {
    padding: 10px;
  }

  .login-form {
    width: 90%;
  }

  .chat__dashboard {
    padding: 10px 10px 50px;
  }

  .chat__new-message {
    padding: 5px 10px;
  }
}

